'use client';

import { useUser } from '@clerk/nextjs';
import { useEffect, useState } from 'react';
import { createClient } from '@/utils/supabase/client';
import Image from 'next/image';

type Event = {
  id: string;
  title: string;
  description: string;
  event_date: string;
  image_url: string;
  tier: 'free' | 'silver' | 'gold' | 'platinum';
};

const tierOrder = ['free', 'silver', 'gold', 'platinum'];

export default function EventsPage() {
  const { user, isLoaded } = useUser();
  const [events, setEvents] = useState<Event[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!isLoaded || !user) return;

    const fetchEvents = async () => {
      const supabase = createClient();

      const userTier = user.publicMetadata.tier as Event['tier'] || 'free';
      const allowedTiers = tierOrder.slice(0, tierOrder.indexOf(userTier) + 1);

      const { data, error } = await supabase
        .from('events')
        .select('*')
        .in('tier', allowedTiers);

      if (error) {
        console.error('Error fetching events:', error.message);
      } else {
        setEvents(data as Event[]);
      }

      setLoading(false);
    };

    fetchEvents();
  }, [isLoaded, user]);

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Your Tier Events</h1>

      {loading ? (
        <p>Loading events...</p>
      ) : (
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {events.map((event) => (
            <div
              key={event.id}
              className="bg-white dark:bg-zinc-900 rounded-xl overflow-hidden shadow-md border"
            >
              <Image
                src={event.image_url}
                alt={event.title}
                width={400}
                height={250}
                className="w-full h-48 object-cover"
              />
              <div className="p-4">
                <h2 className="text-xl font-semibold">{event.title}</h2>
                <p className="text-gray-600 dark:text-gray-300">{event.description}</p>
                <p className="text-sm text-gray-400 mt-2">
                  {new Date(event.event_date).toLocaleDateString()}
                </p>
                <span className={`inline-block mt-2 px-3 py-1 rounded-full text-xs font-medium text-white ${
                  event.tier === 'free' ? 'bg-green-500' :
                  event.tier === 'silver' ? 'bg-gray-500' :
                  event.tier === 'gold' ? 'bg-yellow-500' :
                  'bg-purple-600'
                }`}>
                  {event.tier.toUpperCase()}
                </span>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
